import React from 'react';
import styled from 'styled-components';
import SectionTemplate from 'templates/SectionTemplate';
import H3 from 'components/Text/H3';
import Widgets from 'components/Widgets/Widgets';
import { devices } from 'theme';
import { ReCaptcha } from 'react-recaptcha-google'


const StyledH3 = styled(H3)`
   width: max-content;
   padding: 0 0 20px;

   &:after {
      position: relative;
      top: 5px;
      content: '';
      display: block;
      width: 60%;
      height: 6px;
      background-color: ${({ theme }) => theme.backgrounds.special};
   }
`;

const ContactContainer = styled.div`
   width: 100%;
   background-color: ${({ theme }) => theme.backgrounds.dark};
   display: flex;
   flex-direction: column;

   @media ${devices.laptop} {
      display: grid;
      grid-template-columns: 1fr 1fr;
   }
`;

const FormContainer = styled.div`
   display: flex;
   flex-direction: column;
   padding: 40px;

   form {
      display: flex;
      flex-direction: column;

      @media ${devices.tablet} {
         display: grid;
         grid-template-columns: 1fr 1fr;
         column-gap: 20px;

         > label {
            grid-column: 1/3;

            &:first-of-type { grid-column: 1/2 }
            &:nth-of-type(2) { grid-column: 2/3 }
         }

         > input {
            grid-column: 1/3;
            justify-self: end;
         }
      }
   }

   label {
      position: relative;
      margin: 18px 0;

      input, textarea {
         width: 100%;
         height: 50px;
         padding: 0 15px;
         background-color: ${({ theme }) => theme.backgrounds.light};
         border: 1px solid ${({ theme }) => theme.colors.white};
         border-radius: 3px;
         caret-color: ${({ theme }) => theme.colors.white};
         font-size: 1.7rem;
         color: ${({ theme }) => theme.colors.white};

         &:focus { outline: none };
         &:focus + span, &:valid + span {
            top: -25px;
            padding-left: 0;
            font-size: 1.4rem;
         }
      }

      textarea {
         min-height: 150px;
         resize: vertical;
         padding: 15px 0 0 15px;
      }

      span {
         position: absolute;
         top: 15px;
         left: 0;
         padding-left: 15px;
         font-family: ${({ theme }) => theme.fonts.primary};
         font-size: 1.6rem;
         color: ${({ theme }) => theme.colors.white};
         transition: ${({ theme }) => theme.transitions.ease};
         cursor: text;
      }
   }

   input[type=submit] {
      width: max-content;
      text-align: center;
      padding: 10px 20px;
      border: 2px solid ${({ theme }) => theme.colors.special};
      border-radius: 3px;
      background-color: transparent;
      align-self: flex-end;
      color: ${({ theme }) => theme.colors.white};
      transition: ${({ theme }) => theme.transitions.ease};
      font-weight: bold;
      cursor: pointer;

      &:active, &:hover {
         background-color: ${({ theme }) => theme.backgrounds.special};
         color: ${({ theme }) => theme.colors.black};
      }
   }
`;

const MoreInfoContainer = styled.div`
   display: flex;
   flex-direction: column;
   width: 100%;
   padding: 40px;

   p { padding-top: 10px; }
`;

export default class PageContact extends React.Component {
   constructor(props) {
      super(props);

      this.onLoadRecaptcha = this.onLoadRecaptcha.bind(this);
      this.verifyCallback = this.verifyCallback.bind(this);
   }

   componentDidMount() {
    if (this.captchaDemo) {
        console.log("started, just a second...")
        this.captchaDemo.reset();
        this.captchaDemo.execute();
    }
  }
  onLoadRecaptcha() {
      if (this.captchaDemo) {
          this.captchaDemo.reset();
          this.captchaDemo.execute();
      }
  }
  verifyCallback(recaptchaToken) {
    // Here you will get the final recaptchaToken!!!
    console.log(recaptchaToken, "<= your recaptcha token")
  }

  render() {
     return (
        <SectionTemplate
          id="contact"
          heading="Kontakt"
       >
          <ContactContainer>
             <FormContainer>
                <StyledH3>Napisz do mnie</StyledH3>
                <form
                   action="https://submit-form.com/ytZU1sfGGf_6KGqbf5sgQ"
                   method="POST"
                >
                   <label>
                      <input type="text" name="name" required/>
                      <span>Imie</span>
                   </label>
                   <label>
                      <input type="email" name="email" required/>
                      <span>E-mail</span>
                   </label>
                   <label>
                      <input type="text" name="subject" required/>
                      <span>Temat</span>
                   </label>
                   <label>
                      <textarea name="message" required></textarea>
                      <span>Treść wiadomości</span>
                   </label>

                   <input type="submit" value="Wyślij wiadomość" />
                </form>
             </FormContainer>

             <MoreInfoContainer>
                <StyledH3>Zostańmy w kontakcie</StyledH3>
                <p>Jeżeli chcesz porozmawiać o jakiejkolwiek formie współpracy lub po prostu pozostać w kontakcie będzie mi niezmiernie miło i na pewno odpowiem na wiadomość. Możesz wypełnić formularz lub napisać wiadomość na jednym z poniższych portali.</p>
                <Widgets />
             </MoreInfoContainer>
          </ContactContainer>
          <ReCaptcha
             ref={(el) => {this.captchaDemo = el;}}
             size="invisible"
             render="explicit"
             sitekey="6Ld5Fb4ZAAAAAOeO0zw7RlK-m95HhEPzPE4Tbgbf"
             onloadCallback={this.onLoadRecaptcha}
             verifyCallback={this.verifyCallback}
         />
       </SectionTemplate>
     )
  }
}
